.checkbox {
    position: relative;
    display: flex;
    align-items: center;
}

.checkbox input {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.checkbox span {
    background-color: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    border-radius: 2px;
    height: 18px;
    width: 18px;
    border: 2px solid #3C4043;
}

.checkbox span::before {
    content: '';
    border-color: transparent;
    border-style: solid;
    border-width: 0 2px 2px 0!important;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    margin-top: -2px;
    width: 5px;
    height: 10px;
    margin-left: 1px;
}
.checkbox > input:checked ~ span:before {
    border-color: #3C4043;
    display: block;
}

.radio {
    position: relative;
    display: flex;
    align-items: center;
}

.radio input {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

.radio span {
    background-color: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    border-radius: 2px;
    height: 18px;
    width: 18px;
    border: 2px solid #3C4043;
    border-radius: 50%;
}

.radio span::before {
    content: '';
    display: none;
    height: 8px;
    width: 8px;
    border-radius: 100%;
}

.radio > input:checked ~ span:before {
    background-color: #3C4043;
    display: block;
}
.switch input{
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}
.switch span{
    display: flex;
    position: relative;
    cursor: pointer;
    margin: 2px 0;
    height: 20px;
    width: 42px;
    border-radius: 15px;
    background-color: #aaa;
    align-items: center;
}
.switch span:before{
    content: "";
    display: flex;
    width: 14px;
    height: 14px;
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    left: 4px;
    transition: all 2s ease-out;
}
.switch input:checked ~ span{
    background-color: #6bc259;
}
.switch input:checked ~ span::before{
    left: inherit;
    right: 4px;
}
