         .switch {
          position: relative;
          width: 160px;
          margin: 0 auto;
        }

        .switch-checkbox {
          display: none;
        }


        /*Style words and oval switch */

        .switch-labels {
          display: block;
          overflow: hidden;
          cursor: pointer;
          border-radius: 20px;
        }

        .switch-text {
          display: block;
          width: 200%;
          margin-left: -100%;
          transition: margin 0.3s ease-in 0s;
        }

        .switch-text:before,
        .switch-text:after {
          float: left;
          width: 50%;
          line-height: 30px;
          color: white;
          box-sizing: border-box;
        }

        .switch-text:before {
          content: "OFFICE IN";
          padding-left: 28px;
          font-weight: bold;
          line-height: 40px;
          background-color: #E1F6FF;
          color: #000000;
        }

        .switch-text:after {
          content: "OFFICE OUT";
          padding-right: 22px;
          line-height: 40px;
          font-weight: bold;
          background-color: #4D5585;
          color: #FFFFFF;
          text-align: right;
        }

        /*Style center dot*/

        .switch-dot {
            width: 40px;
            height: 40px;
            background: #FFFFFF;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 120px;
            /* margin-right: 5px; */
            border-radius: 20px;
            transition: all 0.3s ease-in 0s;
        }

        .switch-dot:after{
            content: "";
            position: absolute;
            width: 20px;
            height: 20px;
            background-size: cover;
             margin: 9px 0 0 8px;
        }


        /*State changer*/

        .switch-checkbox:checked+.switch-labels .switch-text {
          margin-left: 0;
        }

        .switch-checkbox:checked+.switch-labels .switch-dot {
          right: 2px;
          margin-right: 0px;
        }   
  
