      .StepProgress {
        position: relative;
        padding-left: 45px;
        list-style: none;
      }

      .StepProgress::before {
        display: inline-block;
        content: '';
        position: absolute;
        top: 0;
        left: 15px;
        width: 10px;
        height: 100%;
        border-left: 2px solid #CCC;
      }

      .StepProgress-item {
        position: relative;
        counter-increment: list;
      }

      .StepProgress-item:not(:last-child) {
        padding-bottom: 20px;
      }

      .StepProgress-item::before {
        display: inline-block;
        content: '';
        position: absolute;
        left: -30px;
        height: 100%;
        width: 10px;
      }

      .StepProgress-item::after {
        content: '';
        display: inline-block;
        position: absolute;
        top: 0;
        left: -37px;
        width: 12px;
        height: 12px;
        border: 2px solid #CCC;
        border-radius: 50%;
        background-color: #FFF;
      }

      .StepProgress-item.is-done::before {
        border-left: 2px solid green;
      }
      
      .StepProgress-item.is-done::after {
        content: "âœ”";
        font-size: 10px;
        color: #FFF;
        text-align: center;
        border: 2px solid green;
        background-color: green;
        height: 18px;
        width: 18px;
      }

      .StepProgress-item.current::before {
        border-left: 2px solid green;
      }
      .StepProgress-item.current::after {
        content: counter(list);
        padding-top: 1px;
        width: 20px;
        height: 20px;
        top: -4px;
        left: -40px;
        font-size: 11px;
        text-align: center;
        color: green;
        border: 2px solid green;
        background-color: white;
      }

      .StepProgress-item.alter-approved::before {
        border-left: 2px solid orange;
      }

      .StepProgress-item.alter-approved::after {
        content: counter(list);
        padding-top: 1px;
        width: 20px;
        height: 20px;
        top: -4px;
        left: -40px;
        font-size: 11px;
        text-align: center;
        color: orange;
        border: 2px solid orange;
        background-color: white;
      }


      .StepProgress-item.not-approved::before {
        border-left: 2px solid red;
      }

      .StepProgress-item.not-approved::after {
        content: counter(list);
        padding-top: 1px;
        width: 20px;
        height: 20px;
        top: -4px;
        left: -40px;
        font-size: 11px;
        text-align: center;
        color: red;
        border: 2px solid red;
        background-color: white;
      }


      .StepProgress strong {
        display: block;
      }

