.custom-tabs .nav-item button{
    border: 0px;
}
.custom-tabs .nav-item .nav-link.active{
    border-bottom: 2px solid;
    font-weight: 600;
}
.payment-method-new .payment-box .card-list-main ul li + li{
    margin-top: 15px;
}
.payment-method-new .payment-box ul .add-card-item input{
    position: absolute;
    left: 60%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 80px;
    height: 24px;
    opacity: 0;
    cursor: pointer;
    z-index: 2;
}
.payment-method-new .payment-box ul .add-card{
    box-shadow: 0px 0px 3px rgb(118 118 118 / 27%);
    flex-direction:inherit;
}
.payment-method-new .payment-box ul .add-card-item input:checked ~ .add-card{
    border-color: #60ba71 !important;
    box-shadow: 0px 0px 9px #60ba717d;
}
.payment-method-new .payment-box ul .add-card .card-img{
    width: 60px;
}
.payment-method-new .payment-box ul .add-card .card-number{
    width: 240px;
}
.payment-method-new .payment-box ul .add-card .lable{
    width: 120px;
}
.label.label-light-success {
    color: #60ba71;
    background-color: #e8fbea;
}
.payment-method-new .payment-box ul .add-card .saprator{
    width: 1px;
    height: 15px;
}
.payment_info_header img{
    width: 110px;
}
.label.label-inline{
    padding: 0.15rem 0.75rem;
    border-radius: 0.42rem;
}
.payment_info_header img{
    width: 100px;
}
@media (min-width: 1600px) {
    .add-card-item{
        display: flex;
    }
    .payment-method-new .payment-box ul .add-card-item input{
        left: 390px;
    }
}
@media (max-width: 1599px) {
    .add-card-item{
        width: 70%;
    }
}
@media (max-width: 1399px) {
    .add-card-item{
        width: 90%;
    }
    .payment-method-new .payment-box ul .add-card .lable{
        width: 210px;
    }
}
@media (max-width: 1199px) {
    .add-card-item{
        width: 100%;
    }
}
@media (max-width: 991px) {
    .add-card-item{
        width: 100%;
    }
}
@media (max-width: 767px) { 
   
}
@media (max-width: 575px) {
    .knowledge-hub-box{
        display: block !important;
        text-align: center;   
    }
    .knowledge-hub-left{
        margin-bottom: 10px;
    }
    .knowledge-hub-left,
    .knowledge-hub-right{
        width: 100% !important;
    }
    .knowledge-hub-left svg{
        width: 65px;
        height: auto;
    }
    .payment-method-new .payment-box ul .add-card .card-img{
        display: inline-block;
        width: 40px;
    }
    .payment-method-new .payment-box ul .add-card .card-number{
        display: inline-block;
        vertical-align: middle;
        width: auto;
    }
    .payment-method-new .payment-box ul .add-card .lable{
        float: right;
        width: auto;
        margin-top: 10px;
    }
    .payment-method-new .payment-box ul .add-card .saprator{
        display: block;
        height: 0px;
    }
    .payment-method-new .payment-box ul .add-card .edit-btn{
        display: inline-block;
        width: 48%;
        text-align: right;
    }
    .payment-method-new .payment-box ul .add-card .delete-btn{
        display: inline-block;
        width: 48%;
        text-align: left;
    }
    .payment-method-new .payment-box ul .add-card-item input{
        top: 10px;
        transform: inherit;
        right: 15px;
        left: inherit;
        width: 100px;
    }
}

